  import urls from '../../utils/urls.js'
  export default {
    data () {
      return {
        content: '',
        ph: '搜索书籍名称、作者名',
        selectList: [],
        history: [],
				hotsearch:[]
      }
    },
    onLoad() {
      if (uni.getStorageSync('history')) {
        this.history = uni.getStorageSync('history')
      }
			
			uni.request({
			  url: urls.HTTP + '/index/hotsearch',
			  method: 'POST',
			  success: (res) => {
			    var arr = []
			    var list = res.data.hotsearch
			    //console.log(list);
			    for (var i = 0; i < list.length; i++) {
			    	var obj = {
							id:list[i].novelid,
			    		title: list[i].title,
			    	}
			    	arr.push(obj)
			    }
			    this.hotsearch = arr
			    // console.log(this.hotsearch);
			  }
			})
    },
    methods: {
      search: function () {
				uni.navigateTo({
					url:"search?content="+this.content
				})
        // console.log(this.selectList)
      //   if (this.selectList.length > 0) {
      //     let arr = this.history
      //     arr.push(this.selectList[0])
      //     this.history = arr
      //     uni.setStorageSync('history', arr)
      //     this.$store.dispatch('history', arr)
      //     this.$store.dispatch('bookId', this.selectList[0].id)
      //     uni.navigateTo({
      //       // url: '../common/book/storyDetail?id='+this.$store.state.bookId
						// url:"search?content="+this.content
      //     })
      //   } else {
      //     uni.showToast({
      //       title: '抱歉，没有你要搜索的书籍',
      //       icon: 'none'
      //     })
      //   }
      },
      goDetail: function (item) {
        console.log(this.history)
        let arr = this.history
        arr.push(item)
        this.history = arr
				console.log(item)
        uni.setStorageSync('history', arr)
        this.$store.dispatch('history', arr)
        this.$store.dispatch('bookId', item.id)
        uni.navigateTo({
          url: '../common/book/storyDetail?id='+this.$store.state.bookId
        })
      },
			goDetails: function (id) {
			  this.$store.dispatch('bookId', id)
			  uni.navigateTo({
			    url: '../common/book/storyDetail?id=' + id
			  })
			},
      getValue: function (e) {
        uni.request({
          url: urls.HTTP + '/index/searchnovel',
          method: 'POST',
          data: {
            keyword: this.content
          },
          success: (res) => {
            console.log(res)
            this.selectList = res.data
          }
        })
      },
      clearHistory: function () {
        uni.showModal({
          title: '提示',
          content: '是否清空所有历史记录？',
          success: (res) => {
            if (res.confirm) {
              this.history = []
              uni.removeStorageSync('history')
              uni.showToast({
                title: '删除成功',
                icon: 'none'
              })
            }
          }
        })
      }
    }
  }
